@keyframes fadeOut1 {

  0%,
  20% {
    opacity: 1
  }

  100% {
    opacity: 0
  }
}

@-webkit-keyframes showIn {
  0% {
    opacity: .0
  }

  100% {
    opacity: 1
  }
}

@keyframes showIn {
  0% {
    opacity: .0
  }

  100% {
    opacity: 1
  }
}

@-webkit-keyframes fadeInRight {
  from {
    opacity: .8;
    transform: translate3d(100%, 0, 0)
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0)
  }
}

@keyframes fadeInRight {
  from {
    opacity: .8;
    transform: translate3d(100%, 0, 0)
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0)
  }
}

@-webkit-keyframes fadeOutRight {

  0%,
  90% {
    opacity: 1
  }

  100% {
    opacity: 0;
    transform: translate3d(110%, 0, 0)
  }
}

@keyframes fadeOutRight {

  0%,
  90% {
    opacity: 1
  }

  100% {
    opacity: 0;
    transform: translate3d(110%, 0, 0)
  }
}

@-webkit-keyframes slidefadeR {
  0% {
    opacity: 0;
    transform: translateX(-10rem)
  }

  80% {
    opacity: .3;
    transform: translateX(2rem)
  }

  90% {
    opacity: .5;
    transform: translateX(-1rem)
  }

  100% {
    opacity: 1;
    transform: translateX(0)
  }
}

@keyframes slidefadeR {
  0% {
    opacity: 0;
    transform: translateX(-10rem)
  }

  80% {
    opacity: .3;
    transform: translateX(2rem)
  }

  90% {
    opacity: .5;
    transform: translateX(-1rem)
  }

  100% {
    opacity: 1;
    transform: translateX(0)
  }
}

@-webkit-keyframes slidefadeD {
  from {
    opacity: .8;
    transform: translate3d(0, -100%, 0)
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0)
  }
}

@keyframes slidefadeD {
  from {
    opacity: .8;
    transform: translate3d(0, -100%, 0)
  }

  to {
    opacity: 1;
    transform: translate3d(0, 0, 0)
  }
}

@-webkit-keyframes slidefadeL {
  0% {
    opacity: 0;
    transform: translateX(10rem)
  }

  80% {
    opacity: .3;
    transform: translateX(-2rem)
  }

  90% {
    opacity: .5;
    transform: translateX(1rem)
  }

  100% {
    opacity: 1;
    transform: translateX(0)
  }
}

@keyframes slidefadeL {
  0% {
    opacity: 0;
    transform: translateX(10rem)
  }

  80% {
    opacity: .3;
    transform: translateX(-2rem)
  }

  90% {
    opacity: .5;
    transform: translateX(1rem)
  }

  100% {
    opacity: 1;
    transform: translateX(0)
  }
}

@-webkit-keyframes BtnUp {
  0% {
    opacity: 0;
    transform: translatey(8rem)
  }

  80% {
    opacity: .3;
    transform: translatey(-2rem)
  }

  90% {
    opacity: .6;
    transform: translatey(1rem)
  }

  100% {
    opacity: 1;
    transform: translatey(0)
  }
}

@keyframes BtnUp {
  0% {
    opacity: 0;
    transform: translatey(8rem)
  }

  80% {
    opacity: .3;
    transform: translatey(-2rem)
  }

  90% {
    opacity: .6;
    transform: translatey(1rem)
  }

  100% {
    opacity: 1;
    transform: translatey(0)
  }
}

.fadeInRight {
  animation: fadeInRight .5s
}

.fadeOutRight {
  animation: fadeOutRight .5s
}

.show {
  display: block
}

.fadeOut {
  animation: fadeOut1 .5s
}

.fadeIn {
  animation: showIn .5s forwards;
  opacity: 1
}


.fadeOut1 {
  animation: fadeOut1 .2s
}

.fadeIn1 {
  animation: showIn .2s forwards;
  opacity: 1
}

.fadeIn2 {
  animation: .3s showIn 0s forwards;
  // opacity: 1
}

.slidefadeL {
  animation: 1s slidefadeL .1s forwards;
  opacity: 1
}

.fadeInRight1 {
  animation: 1s slidefadeR .1s forwards;
  opacity: 1
}

.fadeDown {
  animation: 1s slidefadeD .1s forwards;
  opacity: 1
}

.slideInDown {
  visibility: visible;
  animation: .4s BtnUp .5s forwards;
  opacity: 1
}


@keyframes pulse {
  from {
    transform: scale3d(1, 1, 1);
  }

  50% {
    transform: scale3d(1.05, 1.05, 1.05);
  }

  to {
    transform: scale3d(1, 1, 1);
  }
}